		font-weight: bold;
		span {
			font-family: Barlow, sans-serif;
			color: red;
			font-size: 20px; } }
	&__user-wrapper {
		padding: 0 20px;
		display: flex;
		flex-direction: row;
		justify-content: stretch;
		margin-bottom: 15px; }
	&__first-wrapper {
		border: 1px solid $color-dark;
		border-radius: 100px;
		border-radius: 150px;
		background: url(https://i.ibb.co/sCbwqJj/no-photo.png); }
	&__title {
