.left-wrapper {
	&__pic {
		display: flex;
		width: 150px;
		height: 150px;
		img {
			width: 150px;
			height: 150px;
			border-radius: 100px;
			z-index: 100; } } }

.right-wrapper {
	&__text {
		padding: 30px;
		font-size: 19px;
		font-weight: bold;
		span {
			font-weight: normal;
			margin-left: 5px; } } }
